<template>
  <div>
    <div class="container" v-if="this.is_weixin()">
      <main>
        <div class="rank-top">
          <h6>排行榜</h6>
          <ul>
            <li class="list-item" :class="key == 1?'champion':''" v-for="(item,key) in topData">
              <div class="head-img">
                <div>
                  <div :class="key == 0?'second':key == 1?'first':'third'"></div>
                  <img :src="item.faqi_touxiang" alt="">
                  <span>{{item.supportCount}}</span>
                </div>
              </div>
              <span class="ni-cheng chaochu">{{item.faqi_nicheng}}</span>
              <p class="gailv">{{rewardList[key].reward}}</p>
            </li>
          </ul>
        </div>
      </main>
      <div class="rank-table">
        <div class="t-head">
          <div style="width:20%">排行</div>
          <div style="width:40%">奖励</div>
          <div style="width:25%">名称</div>
          <div style="width:15%">助力值</div>
        </div>
        <div class="t-body">
          <!--<vue-better-scroll-->
          <!--ref="scroll"-->
          <!--:scrollbar="scrollbarObj"-->
          <!--:pullDownRefresh="pullDownRefreshObj"-->
          <!--:pullUpLoad="pullUpLoadObj"-->
          <!--:startY="parseInt(startY)"-->
          <!--@pullingDown="onPullingDown"-->
          <!--@pullingUp="onPullingUp">-->
          <!--</vue-better-scroll>-->
          <table>
            <tr class="item" v-for="(item,key) in pageData.slice(3,50)">
              <td class="paihang">{{key <= 5?'0'+ (key+4):key+4}}</td>
              <td class="jiangli">
                {{key <= 6 ? rewardList[key+3].reward:(key > 6 && key <=
                16)?rewardList[10].reward:rewardList[11].reward}}
              </td>
              <td class="name chaochu">
                <div>
                  <img :src="item.faqi_touxiang" alt="">
                  <span class="chaochu">{{item.faqi_nicheng}}</span>
                </div>
              </td>
              <td class="jifen"><span>{{item.supportCount}}</span></td>
            </tr>
          </table>
        </div>
        <div class="my-rank" v-if="pageData[pageData.length - 1]">
          {{pageData[pageData.length - 1].faqi_nicheng}}：{{pageData[pageData.length - 1].seq}}
        </div>
      </div>
      <div class="bottom">
        <div v-on:click="goBack">返回首页</div>
        <div v-on:click="goToBaiduCampus('show')">我要发起接龙</div>
      </div>
      <div class="jielong-tip" v-show="jlTipShow">
        <div class="jl-tip-box">
          <p>关注百度校园，回复「成语接龙」发起活动</p>
          <div v-on:click="goToBaiduCampus('hide')">好的</div>
        </div>
      </div>
    </div>
    <div v-if="!this.is_weixin()">
      <p style="font-size:20px;text-align: center;margin-top:3rem;padding-bottom:.1rem">长按识别二维码</p>
      <p style="font-size:20px;text-align: center;margin-bottom:.2rem">关注百度校园公众号</p>
      <img src="../../lib/qrCode.png" alt="">
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import qs from 'qs'
  import first from '../../assets/youxuezhilv/first_bg.png'
  import second from '../../assets/youxuezhilv/second.png'
  import third from '../../assets/youxuezhilv/third.png'
  export default {
    name: "RankPage",
    data() {
      return {
        activityId: '',
        faqi_openid: '',
        pageData: [],
        topData: [],
        paihangList:[second,first,third],
        rewardList: [{id: 1, reward: '10倍中奖概率+1500¥奖学金'}, {id: 2, reward: '游学邀请函×1或2000¥奖学金'},
          {
            id: 3, reward: '10倍中奖概率+1000¥奖学金'
          }, {id: 4, reward: '10倍中奖概率+800¥奖学金'},
          {id: 5, reward: '10倍中奖概率+600¥奖学金'}, {id: 6, reward: '10倍中奖概率+500¥奖学金'}, {
            id: 7,
            reward: '10倍中奖概率+400¥奖学金'
          }, {id: 8, reward: '10倍中奖概率+300¥奖学金'},
          {id: 9, reward: '10倍中奖概率+200¥奖学金'}, {id: 10, reward: '10倍中奖概率+100¥奖学金'}, {
            id: 11,
            reward: '5倍中奖概率+百度小熊×1'
          }, {id: 12, reward: '2倍中奖概率+百度小熊×1'}],
        sliceCount: 10,
        // 这个配置可以开启滚动条，默认为 false。当设置为 true 或者是一个 Object 的时候，都会开启滚动条，默认是会 fade 的
        scrollbarObj: {
          fade: true
        },
        // 这个配置用于做下拉刷新功能，默认为 false。当设置为 true 或者是一个 Object 的时候，可以开启下拉刷新，可以配置顶部下拉的距离（threshold） 来决定刷新时机以及回弹停留的距离（stop）
        pullDownRefreshObj: {
          threshold: 60,
          stop: 40,
          txt: '刷新成功'
        },
        // 这个配置用于做上拉加载功能，默认为 false。当设置为 true 或者是一个 Object 的时候，可以开启上拉加载，可以配置离底部距离阈值（threshold）来决定开始加载的时机
        pullUpLoadObj: {
          threshold: 0,
          txt: {
            more: '加载更多',
            noMore: '没有更多了'
          }
        },
        startY: 0, // 纵轴方向初始化位置
        scrollToX: 0,
        scrollToY: 0,
        scrollToTime: 700,
        baiduCampusUrl: 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MjM5NjIwOTQyMA==&scene=126&bizpsid=0#wechat_redirect',
        jlTipShow:false

      }
    },
    mounted() {
      if (this.getQueryParams('activityId') && this.getQueryParams('faqi_openid')) {
        this.activityId = this.getQueryParams('activityId');
        this.faqi_openid = this.getQueryParams('faqi_openid');
        this.getData()
      }
    },

    methods: {
      getData(str) {
        let data = {
          activityId: this.activityId,
          faqi_openid: this.faqi_openid
        }
        return new Promise((resolve, reject) => {
          axios.post(this.pc_proxy + 'queryJieLongRank.do', qs.stringify(data)).then(res => {
            if (res.status === 200) {
              if (res.data.error_no == '0') {
                if (str == 'refresh') {
                  this.pageData = [];
                  this.topData = [];
                }
                for (var j = 0; j < res.data.data.length; j++) {
                  this.pageData.push(res.data.data[j]);
                }
                this.topData.push(this.pageData[1]);
                this.topData.push(this.pageData[0]);
                this.topData.push(this.pageData[2]);
                resolve('ok');
              }
            }
          })
        })
      },
      onPullingDown() {
        // 下拉刷新
        this.sliceCount = 10;
        this.getData('refresh').then(res => {
          this.$refs.scroll.forceUpdate(true)
        }, (err) => {

        })
      },
      onPullingUp() {
        // 上拉加载
        this.showMore().then(res => {
          if (this.sliceCount >= 50) {
            this.$refs.scroll.forceUpdate(false)
          } else {
            this.$refs.scroll.forceUpdate(true);
          }
        })
      },
      showMore() {
        return new Promise((resolve, reject) => {
          setTimeout(() => {
            this.sliceCount += 10;
            if (this.sliceCount > 50) {
              this.sliceCount = 50;
            }
            resolve(this.sliceCount);
          }, 800)
        })
      },
      goBack() {
        this.$router.push({path: '/'});
      },
      goToBaiduCampus(str) {
        if(str == 'show'){
          this.jlTipShow = true
        }
        if(str == 'hide'){
          this.jlTipShow = false
        }
      }
    },
  }
</script>

<style scoped lang="stylus">
  @import "../../assets/icon/font.css"
  $blue = #4f71a3
  $lightBlue = #49b8d4
  $yellow = #ffe509
  .chaochu {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .container {
    font-family 'fzzy' !important
    padding 0 .25rem
    min-height 13.34rem
    background url("../../assets/youxuezhilv/bg01.png") center center
    background-repeat repeat-y
    background-size contain
    overflow hidden
    main {
      width 100%
      height 5rem
      background url("../../assets/youxuezhilv/bg02.png") no-repeat
      background-position: top left
      background-size cover
      overflow hidden
      margin-top .4rem
      .rank-top {
        width 100%
        height auto
        h6 {
          color #ffffff
          text-align center
          line-height 1rem
          font-size .5rem
        }
        ul {
          width 6.35rem
          height 100%
          margin 0 auto
          margin-top .6rem
          display flex
          align-items center
          justify-content space-around
          .list-item {
            width 2rem
            .head-img {
              margin 0 auto
              position relative
              width 100%
              div {
                position relative
                width 1.3rem
                height 1.3rem
                margin 0 auto
                .first{
                  width .51rem
                  height .47rem
                  background url("../../assets/youxuezhilv/first_bg.png") no-repeat center center
                  -webkit-background-size: cover
                  background-size: cover
                  position absolute
                  top 0
                  right 0
                  z-index: 99
                }
                .second{
                  width .35rem
                  height .33rem
                  background url("../../assets/youxuezhilv/second.png") no-repeat center center
                  -webkit-background-size: cover
                  background-size: cover
                  position absolute
                  top -0.13rem
                  right 0.08rem
                  z-index: 99
                }
                .third{
                  width .35rem
                  height .33rem
                  background url("../../assets/youxuezhilv/third.png") no-repeat center center
                  -webkit-background-size: cover
                  background-size: cover
                  position absolute
                  top -0.13rem
                  right 0.08rem
                  z-index: 99
                }
                img {
                  width 1.3rem
                  height 1.3rem
                  border-radius 50%
                  display block
                  margin 0 auto
                }
                span {
                  display block
                  padding 0rem .25rem
                  background-color $yellow
                  border-radius 10px
                  font-size .25rem
                  color #000000
                  position absolute
                  bottom 0
                  right 0
                }
              }
            }
            .ni-cheng {
              display block
              font-size .25rem
              color #000000
              text-align center
              margin-top .15rem
            }
            .gailv {
              font-size .25rem
              color: $blue
              text-align center
            }
          }
          .champion {
            width 3rem
            .head-img {
              div {
                width 2rem
                height 2rem
                margin 0 auto
                position relative
                img {
                  width 2rem
                  height 2rem
                }
              }
            }
          }
        }
      }
    }
    .rank-table {
      background #ffffff
      .t-head {
        overflow hidden
        width 100%
        > div {
          font-size .3rem
          text-align center
          background $lightBlue
          font-weight normal
          color #ffffff
          float left
        }
      }
      .t-body {
        height auto
        font-size .12rem
        position relative
        table {
          width 100%
          border-collapse: separate;
          border-spacing: 0px 8px;
          tr {
            width 100%
            height .5rem
            overflow hidden
            td {
              font-size .25rem
              text-align center
              line-height .5rem
              width 25%
              img {
                width .4rem
                height .4rem
                margin-top .05rem
                border-radius 50%
                display block
                float left
              }
              p {
                background $yellow
                padding .12rem .2rem
                font-size .24rem
                text-align center

              }
              > span {
                padding 0rem .24rem
                border-radius 10px
                background $yellow
              }
            }
            .paihang {
              width 10%
              color $lightBlue
              font-weight bold
              font-size .3rem
            }
            .jiangli {
              width 50%
              color $blue
              font-size .25rem
            }
            .name {
              width 25%
              > div {
                overflow hidden

                span {
                  display block
                  float left
                  width 1.3rem
                  font-size .25rem
                  color #999999
                }
              }
            }
            .jifen {
              width 15%
              font-size .25rem
            }
          }
        }
      }
      .my-rank {
        font-size .35rem
        text-align center
        color $lightBlue
        padding-bottom .2rem
      }
    }
    .bottom {
      display flex
      align-items center
      justify-content space-between
      padding .5rem 0.4rem
      > div {
        width 2.85rem
        height .65rem
        text-align center
        line-height .65rem
        border-radius 20px
        font-size .35rem
      }
      > div:first-child {
        background-color #c6dee8
        color #406aa1
      }
      > div:nth-child(2) {
        background-color #ffd507
        color #5c5c5c
      }
    }
    .jielong-tip {
      position fixed
      background rgba(0, 0, 0, 0.3)
      top 0
      left 0
      bottom 0
      right 0
      z-index: 999
      display flex
      align-items center
      justify-content center
      .jl-tip-box{
        width 5rem
        height 2.2rem
        background #ffffff
        border-radius 5px
        overflow hidden
        margin-bottom 1rem
        > p {
          padding:.2rem .1rem
          font-size .3rem
          color rgba(0,0,0,0.8)
          text-align center
          margin-top .2rem
          border-bottom: 1px solid rgba(0,0,0,0.3)
          box-sizing border-box
        }
        div {
          font-size .25rem
          color dodgerblue
          text-align center
          padding .25rem 0
        }
      }
    }
  }
</style>



